.Results_Container {
  padding-top: 82px;
  min-height: 200vh;
  font-family: "Lato", sans-serif;
  background-color: #1b1e24;
  margin-bottom: 500px;
  width: 100%;
}

.Results {
  float: left;
  overflow-y: auto;
  position: fixed;
  height: calc(100vh - 75px);
  margin-bottom: 2000px;
  width: 50%;
}

.Map {
  float: right;
  width: 50%;
  border: 2px solid #eeeeee;
  height: 100vh;
  position: fixed;
  left: 50%;
}

.Map_Text {
  margin-top: 50%;
  margin-left: 50%;
  font-size: 36px;
}

.results-box {
  margin-left: 80px;
  width: 80%;
}

.results-search {
  position: fixed;
}



