.navbar {
  /* background-color: #ffffff; */
  background-color: rgba(0, 0, 0, 0.2);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  color: white;
  text-decoration: none;
  position: fixed;
  width: 100%;
  z-index: 9999;
  height: 73px;
}

.navbar a {
  text-decoration: none;
  color: black;
  width: 100px;
}

.Login_Main {
  width: 316px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.Login_Main a {
  width: 70px;
}

.search-button :hover {
  text-decoration: none;
  opacity: 0.5;
  -webkit-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}

.Logo_Main :hover {
  text-decoration: none;
  opacity: 0.5;
  -webkit-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}

.Login_Toggle {
  width: 317px;
}

.nav-button {
  text-decoration: none;
  text-transform: uppercase;
  padding-top: 15px;
  padding-right: 24px;
  color: white;
  font-family: "Lato", sans-serif;
  font-size: 15px;
  letter-spacing: 1px;
  text-align: right;
}

.login-text :hover {
  text-decoration: none;
  opacity: 0.5;
  -webkit-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}

.Toggle-Menu {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.profile-img {
  height: 42px;
  width: 42px;
  border-radius: 50%;
  margin-top: 18px;
  margin-right: 3px;
}

.fa-caret-down {
  margin-top: 29px;
  margin-right: 24px;
}

.drawer {
  background-color: rgba(0, 0, 0, 0.8) !important;
  padding-top: 20px;
}

.menu-item {
  color: #515151 !important;
  font-family: "Lato", sans-serif !important;
  text-transform: uppercase;
  text-align: left;
}

.menu-item :hover {
  text-decoration: none;
  color: rgb(222, 222, 222);
  -webkit-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}

.toggle-line {
  /* color: #262626; */
  margin: 0;
}

.search-bar {
  width: 250px;
  height: 30px;
  margin-top: 24px;
  margin-left: 24px;
  padding-left: 16px;
  background: rgba(0, 0, 0, 0.3);
  border: 0;
  border-radius: 25px;
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  color: white;
}

textarea:focus,
input:focus {
  outline: none;
}

.search-button {
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
  outline: none;
  color: white;
  font-size: 15px;
}



